"use client";

import { useState } from "react";

export default function Home() {
  const [prompt, setPrompt] = useState("");
  const [gptResult, setGptResult] = useState("");
  const [backtestResult, setBacktestResult] = useState<any>(null);
  const [loadingGPT, setLoadingGPT] = useState(false);
  const [loadingBacktest, setLoadingBacktest] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoadingGPT(true);
    setGptResult("");

    try {
      const response = await fetch("http://127.0.0.1:8000/generate", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ prompt }),
      });

      const data = await response.json();
      setGptResult(data.result);
    } catch (error) {
      setGptResult("Error connecting to backend.");
    } finally {
      setLoadingGPT(false);
    }
  };

  const runBacktest = async () => {
    setLoadingBacktest(true);
    setBacktestResult(null);

    try {
      const response = await fetch("http://127.0.0.1:8000/backtest");
      const data = await response.json();
      setBacktestResult(data);
    } catch (error) {
      setBacktestResult({ error: "Error connecting to backend." });
    } finally {
      setLoadingBacktest(false);
    }
  };

  return (
    <main className="flex min-h-screen items-center justify-center p-10 bg-gray-100">
      <div className="max-w-xl w-full space-y-6">
        <h1 className="text-3xl font-bold text-center">Backtest Anything</h1>

        {/* Prompt input form */}
        <form onSubmit={handleSubmit} className="space-y-4">
          <textarea
            className="w-full p-4 border rounded-lg"
            placeholder="e.g. Buy BTC when it drops 5% and sell after it rises 10%"
            rows={4}
            value={prompt}
            onChange={(e) => setPrompt(e.target.value)}
          />
          <button
            type="submit"
            className="w-full bg-black text-white p-3 rounded-lg hover:bg-gray-800"
            disabled={loadingGPT}
          >
            {loadingGPT ? "Generating..." : "Generate Strategy Code"}
          </button>
        </form>

        {/* GPT result display */}
        {gptResult && (
          <div className="bg-white p-4 rounded-lg border whitespace-pre-wrap">
            <h2 className="font-semibold mb-2">Generated Code:</h2>
            <pre>{gptResult}</pre>
          </div>
        )}

        {/* Backtest button */}
        <button
          onClick={runBacktest}
          className="w-full bg-blue-600 text-white p-3 rounded-lg hover:bg-blue-700 disabled:opacity-60"
          disabled={loadingBacktest}
        >
          {loadingBacktest ? "Running Backtest..." : "Run Backtest"}
        </button>

        {/* Backtest result display */}
        {backtestResult && (
          <div className="bg-white p-4 rounded-lg border">
            <h2 className="font-semibold mb-2">Backtest Result:</h2>
            <pre className="whitespace-pre-wrap">
              {JSON.stringify(backtestResult, null, 2)}
            </pre>
          </div>
        )}
      </div>
    </main>
  );
}
